#app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;

    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.logo {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    z-index: 99;
    width: 38%;
    max-width: 38%;
    margin: 0 auto;
    display: flex;
    pointer-events: none;
    opacity: 0;

    transition: opacity 1.2s !important;
}
.logo img {
    max-width: 100% !important;
}
@media (max-width: 992px) {
    .logo {
        width: 62%;
        max-width: initial;
    }
}
/* #endregion */

/*  #region Preloader Animation */
.loadContainer {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: black;
    z-index: 999;
    transition: opacity 0.25s;
}
.loader {
    height: 5px;
    width: 0px;
    background: #fff;
    position: absolute;
    /* transition: opacity .25s; */
    bottom: 0;
    left: 0;
    /* transform: translateY(-50%);  */
}
/*  #endregion */
